<template>
  <v-container fluid grid-list-xs>

    <div class="row mb-3">
      <div class="row">
        <div class="col texy-left"></div>
        <v-btn color="blue darken-2 mnl-5" dark large @click="RegresarUsuarios()"><b>
            <h4 class="dinline mr-2"><i class="fas fa-arrow-left mt-2"></i></h4>Regresar
          </b></v-btn>
      </div>
    </div> 

    <div class="row text-center">

      <div class="col-md-7 col-lg-6 col-xl-8 order-2  order-md-1 order-lg-1 order-xl-1 mt-2">
         <h6 class="text-pre-wrap font-weight-bold headline">EVALUACION DE DESEMPEO</h6>        
        <div class="text-right">
          <v-btn color="light-blue darken-4" dark large v-on:click="GuardarEvaluacion"><b>Guardar</b></v-btn>
        </div>
        <aHistorialDiciplinario></aHistorialDiciplinario>
        <aReactivosPersonal class="mt-3"> </aReactivosPersonal>
      </div>
      <div class="col-md-5 col-lg-5 col-xl-4 order-1 order-md-2 order-lg-2 order-xl-2  mt-2">
        <infoEmpleado class=""></infoEmpleado>
        <firmasyResultados class="mt-lg-5"></firmasyResultados>
      </div>


    </div>

  </v-container>


</template>

<script>
  import infoEmpleado from './childs/infoEmpleado.vue';
  import firmasyResultados from './childs/firmasyResultados.vue';

  import aHistorialDiciplinario from './childs/aHistorialDiciplinario.vue';
  import aReactivosPersonal from './childs/aReactivosPersonal.vue';

  export default {
    name: 'evaluarEmpleado',
    components: {

      infoEmpleado,
      firmasyResultados,
      aHistorialDiciplinario,
      aReactivosPersonal
    },
    data() {
      return {}
    },
    computed: {},
    methods: {
      GuardarEvaluacion: function () {
         this.$store.dispatch("s_Loading", { value: 0, show: true });
        this.$store.dispatch('saveUpdateUser');
      },
      RegresarUsuarios(){
         var esto = this,
         headers = [
            {
              text: "",
              value: "Image"
            },
            {
              text: "Nombre",
              value: "PrettyName"
            },
            {
              text: "Clasificacin",
              value: "Clasificacion"
            },
            {
              text: "Puesto",
              value: "Position"
            },
            {
              text: "Status",
              value: "Status"
            },
            {
              text: "",
              value: "Actions"
            }
          ];
          esto.$store.dispatch("s_Loading", { value: 0, show: true });
          esto.$store.dispatch("GetSubordinateByUser", {
            code: 'employeetoevaluate',
            headers: headers          
          });
        
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../../../assets/css/global.css';
  @import '../../../assets/css/media_query.css';

  .theme--dark.v-toolbar {
    background-color: #1e88e5 !important;
    color: #fff !important;
  }

</style>



