<template>

  <div class=" mt-2">

    <!-- MENU PRINCIPAL -->

    <v-navigation-drawer fixed :clipped="$vuetify.breakpoint.mdAndUp" app v-model="drawer">
      <v-list dense>
        <template v-for="item in items">
          <v-layout row v-if="item.heading" align-center :key="item.heading">
            <v-flex xs6>
              <v-subheader v-if="item.heading">
                {{ item.heading }}
              </v-subheader>
            </v-flex>
          </v-layout>
          <v-list-group v-else-if="item.children" v-model="item.model" :key="item.text"
            :prepend-icon="item.model ? item.icon : item['icon-alt']" append-icon="">
            <v-list-tile slot="activator">
              <v-list-tile-content>
                <v-list-tile-title>
                  <b class="h4"> {{ item.text }} </b>
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile v-for="(child, i) in item.children" :key="i" @click="clickMenu(child)">
              <v-list-tile-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ child.text }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
          <v-list-tile v-else @click="clickMenu(item)" :key="item.text">
            <v-list-tile-action>
              <v-icon> {{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                {{ item.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar dark app :clipped-left="$vuetify.breakpoint.mdAndUp" fixed>
      <v-toolbar-title style="width: 500px" class="ml-0 pl-3">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <img src="../assets/img/logow.png" width="200px" class="mr-2" />
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <span class="mr-3">{{g_loginUser.Name}}</span>       
      <v-btn icon large>
        <v-avatar size="50px" tile>
          <img :src="g_loginUser.UserPic" alt="Vuetify" class="iconmenu rounded-circle">
        </v-avatar>
      </v-btn>
    </v-toolbar>
    <v-content>
      <v-container fluid fill-height>
        <v-layout>
          <!-- Componentes -->
          <listaEmpleadosEvaluar v-if="gsw_uimainEvaluacion ==='employeetoevaluate'" >
          </listaEmpleadosEvaluar>

          <home v-if="gsw_uimainEvaluacion =='home'"></home>

          <evaluarempleado v-if="gsw_uimainEvaluacion =='evaluarEmpleado'"></evaluarempleado>



        </v-layout>
      </v-container>
      <cdialog v-if="gsw_dialog"></cdialog>
    </v-content>
    <!--  <v-btn fab bottom left color="pink" dark fixed @click.stop="dialog = !dialog">
      <v-icon>add</v-icon>
    </v-btn>
    -->

    <v-dialog v-model="dialog" width="800px">
   
    </v-dialog>i



  </div>

</template>

<script>
import listaEmpleadosEvaluar from "./modules/listaevaluacion/listaEmpleadosEvaluar.vue";
import evaluarempleado from "./modules/evaluarempleado/evaluarempleado.vue";
import home from "./modules/home/home.vue";
import cdialog from "./elemento/cdialog.vue";

export default {
  name: "mainEvaluacion",
  components: {
    listaEmpleadosEvaluar,
    evaluarempleado,
    home,
    cdialog
  },
  data() {
    return {
      dialog: false,
      drawer: null,     
      items: [
        {
          icon: "fas fa-home",
          text: "home",
          code: "home"
        },
        {
          icon: "far fa-list-alt indigo--text text--darken-4",
          text: "Empleados a Evaluar",
          code: "employeetoevaluate"
        },
        {
          icon: "fas fa-sign-out-alt fa-rotate-180",
          text: "Cerrar sesin",
          code: "Salir"
        }
      ]
    };
  },
  computed: {
    g_loginUser() {
      return this.$store.getters.g_loginUser;
    },
    gsw_uimainEvaluacion() {
      return this.$store.getters.gsw_uimainEvaluacion;
    },
    gsw_dialog() {
      return this.$store.getters.gsw_dialog;
    },
    g_showMessage() {
      this.$store.getters.g_showMessage;
    }
  },
  methods: {
    clickMenu(child) {
      var esto = this;
      var headers = "";
      switch (child.code) {
        case "Salir":
          this.$store.dispatch("gsw_ui", "login");
          this.$store.dispatch("sw_uimainEvaluacion", "home");
          break;
        case "home":
          this.$store.dispatch("sw_uimainEvaluacion", "home");
          break;
        case "employeetoevaluate":
          headers = [
            {
              text: "",
              value: "Image"
            },
             {
              text: "Num Emp",
              value: "Number"
            },
            {
              text: "Nombre",
              value: "PrettyName"
            },
            {
              text: "Clasificacin",
              value: "Clasificacion"
            },
            {
              text: "Puesto",
              value: "Position"
            },
            {
              text: "Status",
              value: "Status"
            },
            {
              text: "",
              value: "Actions"
            }
          ];
          esto.$store.dispatch("s_Loading", { value: 0, show: true });
          esto.$store.dispatch("GetSubordinateByUser", {
            code: child.code,
            headers: headers           
          });
          break;
        case "employeetoevaluateRH":
          headers = [
            {
              text: "",
              value: "Image"
            },
            {
              text: "Num Emp",
              value: "Number"
            },
            {
              text: "Nombre",
              value: "PrettyName"
            },
            {
              text: "Clasificacin",
              value: "Clasificacion"
            },
            {
              text: "Puesto",
              value: "Position"
            },
            {
              text: "Iniciado",
              value: "Initiated"
            },
            {
              text: "Completado",
              value: "Actions"
            },
            {
              text: "Total Evaluados",
              value: "TotalEvaluate"
            },
            {
              text: "",
              value: "Actions"
            }
          ];
          esto.$store.dispatch("s_Loading", { value: 0, show: true });
          esto.$store.dispatch("GetSummarySubordinates", {
            code: child.code,
            headers: headers          
          });
          break;
      }
    }
  },
  mounted: function() {
    var esto = this;
    if (esto.g_loginUser.isRH) {
      esto.items.splice(2, 0, {
        icon: "fa-id-card blue--text text--darken-1",
        text: "Empleados que estan Evaluando",
        code: "employeetoevaluateRH",
        
      });
    }
  }
};
</script>

<style scoped>
@import "../assets/css/global.css";

.theme--dark.v-toolbar {
  background-color: black !important;
  color: #fff !important;
}

.overflow {
  width: 10em;
  outline: 1px solid #000;
  margin: 0 0 2em 0;

  /**
   * Required properties to achieve text-overflow
   */
  white-space: nowrap;
  overflow: hidden;
}

body style {
  display: block;
  font: 14px monospace;
  padding: 3px;
  margin: 0 0 5px 0;
}

.v-list__tile__title{
font-size: 1em !important;
}
</style>



