<template>

  <div class="container-fluid">

    <v-btn color="blue darken-2 mnl-5" v-if="g_gridby ==='employesScore'"  dark large @click="RegresarUsuariosRH()"><b>
      <h4 class="dinline mr-2"><i class="fas fa-arrow-left  mt-2"></i></h4>Regresar</b></v-btn>
      

    <v-toolbar dark color="indigo darken-3 " v-if="g_gridby === 'employeetoevaluate'">
      <v-toolbar-title class="white--text ">Empleados a evaluar
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <h1><i class="far fa-list-alt mt-2"></i></h1>
    </v-toolbar>

    <v-toolbar dark color="blue darken-1" v-if="g_gridby === 'employeetoevaluateRH'">
      <v-toolbar-title class="white--text" v-if="g_gridby === 'employeetoevaluateRH'">Supervisores Evaluadores
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <h1><i class="fas fa-id-card mt-2"></i></h1>
    </v-toolbar>

    <v-toolbar dark color="teal lighten-1" v-if="g_gridby === 'employesScore'">
      <v-toolbar-title class="white--text" v-if="g_gridby === 'employesScore'">Empleados Evaluados por el Supervisor
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <h1><i class="fas fa-users mt-2"></i></h1>
    </v-toolbar>

    <br>
    <v-toolbar light color="" class="">
      <v-spacer></v-spacer>
      <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
    </v-toolbar>
    <v-layout v-resize="onResize" column>
      <v-data-table :headers="g_headers" :items="ListSubordinate" :search="search" :custom-filter="customFilter" :pagination.sync="pagination"
        :rows-per-page-items="pagination.rowsPerPageItems" :hide-headers="isMobile" :class="{mobile: isMobile}"  >

        <template slot="items" slot-scope="props">
          <tr v-if="!isMobile">
            <td class="">
              <img v-if="(g_gridby === 'employeetoevaluate') || (g_gridby ==='employesScore')" :src="props.item.Image"
                class="iconsize rounded-circle mt-1 mb-1 " /></td>
            <td class=""><b>#{{props.item.Number}}</b> </td>
            <td class="">{{ props.item.PrettyName }} </td>
            <td class="">{{ props.item.Clasificacion }}</td>
            <td class="">{{ props.item.Position}}</td>
            <td class="" v-if="g_gridby === 'employeetoevaluate'">{{ props.item.Status}}</td>
            <td class="" v-if="g_gridby === 'employeetoevaluateRH'">{{ props.item.Initiated }}</td>
            <td class="" v-if="g_gridby === 'employeetoevaluateRH'">{{ props.item.Completed }}</td>
            <td class="" v-if="g_gridby === 'employeetoevaluateRH'">{{ props.item.TotalEvaluate }}</td>
            <td class="" v-if="g_gridby === 'employesScore'">
              <v-rating v-model="props.item.Score" background-color="orange" color="orange" readonly="readonly" medium>
              </v-rating>
            </td>
            <td class="" v-if="g_gridby === 'employeetoevaluate'">
              <v-btn v-if="props.item.Status === 'NO INICIADO'" color="indigo darken-4  w-75" class=" mb-2" dark large
                @click="EvaluarEmpleado(props.item)"><b>EVALUAR</b></v-btn>
              <v-btn v-else color="blue darken-3 mb-2 font-bold  w-75" class="" dark large @click="EvaluarEmpleado(props.item)">
                <b>EDITAR</b></v-btn>
            </td>
            <td v-if="g_gridby === 'employeetoevaluateRH'">
              <v-btn color="blue darken-1 w-75 mb-2" dark large @click="Vizualizar(props.item)"><b>Visualizar</b>
              </v-btn>
            </td>
          </tr>
          <tr v-else>
            <td>
              <ul class="flex-content">
                <li class="flex-item mt-2 mb-2"
                  v-if="(g_gridby === 'employeetoevaluate') || (g_gridby ==='employesScore')">
                  <img :src="props.item.Image" class="iconsize rounded-circle" /> </li>
                <li class="flex-item mt-2 mb-2" data-label="Name">{{ props.item.Name }}</li>
                <li class="flex-item mt-2 mb-2" data-label="#Number"> # {{ props.item.Number}}</li>
                <li class="flex-item mb-5" data-label="Position"> {{props.item.Position}} </li>
                <li class="flex-item mt-2 mb-2 " data-label="Status" v-if="g_gridby === 'employeetoevaluate'">
                  {{props.item.Status}}
                </li>
                <li class="flex-item mt-2 mb-2" data-label="Initiated" v-if="g_gridby === 'employeetoevaluateRH'">
                  {{props.item.Initiated}}
                </li>
                <li class="flex-item mt-2 mb-2" data-label="COMPLETED" v-if="g_gridby === 'employeetoevaluateRH'">
                  {{props.item.Completed}}
                </li>
                <li class="flex-item mt-2 mb-2" data-label="TOTALEVALUATE" v-if="g_gridby === 'employeetoevaluateRH'">
                  {{props.item.TotalEvaluate}}
                </li>
                <li class="flex-item mt-4 mb-4" v-if="g_gridby === 'employeetoevaluate'">
                  <v-btn v-if="props.item.Status !== 'COMPLETADO'" dark large class="indigo darken-4 w-75 mb-2" 
                    @click="EvaluarEmpleado(props.item)"><b>Evaluar</b></v-btn>
                  <v-btn v-else color="indigo darken-3 w-75 mb-2 font-bold" dark large @click="EvaluarEmpleado(props.item)">
                    <b>Editar</b></v-btn>
                <li>
                <li class="flex-item mt-4 mb-4" v-if="g_gridby === 'employeetoevaluateRH'">
                  <v-btn color="blue darken-2  mb-2" dark large @click="Vizualizar(props.item)"><b>Visualizar</b>
                  </v-btn>
                </li>
                <li class="flex-xs12 mt-4 mb-4" v-if="g_gridby === 'employesScore'">
                  <v-rating v-model="props.item.Score" background-color="orange" color="orange" readonly="readonly"
                    small>
                  </v-rating>
                </li>
              </ul>

            </td>
          </tr>
        </template>
        <v-alert slot="no-results" :value="true" color="red lighten-1" icon="warning ">
          Your search for "{{ search }}" found no results.
        </v-alert>
      </v-data-table>
    </v-layout>

  </div>

</template>

<script>
  export default {
    name: "listaEmpleadosEvaluar",
    data() {
      return {
        mostrarEnviar: false,
        selected: [],
        search: "",
        isMobile: false,
        font: "font-size: 20px !important;",
        //  headers: [],
        g_gridby: "",
        pagination: {
          descending: true,
          page: 1,
          rowsPerPage: 10, // -1 for All
          sortBy: 'name',
          rowsPerPageItems: [10, 50, 100, 150, 200]
        }
      };
    },
    computed: {
      ListSubordinate() {
        this.g_gridby = this.$store.getters.g_loginUser.gridby;
        this.search = '';
        return this.$store.getters.g_loginUser.Subordinates;
      },
      g_headers() {
        return this.$store.getters.g_headers;
      }
    },
    methods: {
      onResize() {
        if (window.innerWidth < 910) this.isMobile = true;
        else this.isMobile = false;
      },
      toggleAll() {
        if (this.selected.length) this.selected = [];
        else this.selected = this.desserts.slice();
      },
      changeSort(column) {
        if (this.pagination.sortBy === column) {
          this.pagination.descending = !this.pagination.descending;
        } else {
          this.pagination.sortBy = column;
          this.pagination.descending = false;
        }
      },
      customFilter(items, search, filter) {

              //this custom filter will do a multi-match separated by a space.
                //e.g

                if (!search) { return items } //if the search is empty just return all the items

                function  new_filter (val, search) {
                    return val !== null &&
                        ['undefined', 'boolean'].indexOf(typeof val) === -1 &&
                        val.toString().toLowerCase().replace(/[^0-9a-zA-Z]+/g,"").indexOf(search) !== -1
                }

                let needleAry = search.toString().toLowerCase().split(" ").filter(x => x);
                //whenever we encounter a space in our search we will filter for both the first and second word (or third word)

                return items.filter(row => needleAry.every(needle => Object.keys(row).some(key => new_filter(row[key],needle))));
                //foreach needle in our array cycle through the data (row[key]) in the current row looking for a match.
                // .some will return true and exit the loop if it finds one it will return false if it doesnt
                // .every will exit the loop if we dont find a match but will return true if all needles match
                // .filter the rows on each match


        },

      Vizualizar(item) {
        var esto = this;

        var headers = [{
            text: "",
            value: "Image"
          },
            {
              text: "Num Emp",
              value: "Number"
            },
          {
            text: "Nombre",
            value: "PrettyName"
          },
          {
            text: "Clasificacin",
            value: "Clasificacion"
          },
          {
            text: "Puesto",
            value: "Position"
          },
          {
            text: "Score",
            value: "Score"
          }
        ];
        esto.$store.dispatch("s_Loading", {
          value: 0,
          show: true
        });
        esto.$store.dispatch("GetEmployeesBySuperviser", {
          number: item.Number,
          code: "employesScore",
          headers: headers
        });
      },
      EvaluarEmpleado(empleadoSelectInfo) {
        var condition =
          empleadoSelectInfo.Status == "COMPLETADO" ? "Editar" : "Evaluar";
        var esnuevo = empleadoSelectInfo.Status == "COMPLETADO" ? false : true;

        var dialog = {
          Value: true,
          Title: condition + " Empleado",
          Subtitle: "Ests Seguro de " + condition + " al siguiente Empleado?",
          contieneImagen: true,
          Image: empleadoSelectInfo.Image,
          Paragraph: empleadoSelectInfo.PrettyName,
          esNuevo: esnuevo,
          Number: empleadoSelectInfo.Number
        };

        this.$store.dispatch("sw_dialog", dialog);
        this.$store.dispatch("set_cDialog", empleadoSelectInfo);
      },
      RegresarUsuariosRH(){
        var esto = this,
         headers = [
            {
              text: "",
              value: "Image"
            },
             {
              text: "Num Emp",
              value: "Number"
            },
            {
              text: "Nombre",
              value: "PrettyName"
            },
            {
              text: "Clasificacin",
              value: "Clasificacion"
            },
            {
              text: "Puesto",
              value: "Position"
            },
            {
              text: "Initiated",
              value: "Initiated"
            },
            {
              text: "Completed",
              value: "Actions"
            },
            {
              text: "TotalEvaluate",
              value: "TotalEvaluate"
            },
            {
              text: "",
              value: "Actions"
            }
          ];
          esto.$store.dispatch("s_Loading", { value: 0, show: true });
          esto.$store.dispatch("GetSummarySubordinates", {
            code: 'employeetoevaluateRH',
            headers: headers          
          });
      }
    },
    mounted: function () {
      //    var esto = this;
      //   esto.headers = esto.heades;
      //   esto.g_gridby = this.$store.getters.g_gridby;
    }
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../../../assets/css/global.css";
  @import "../../../assets/css/media_query.css";

  /* .theme--dark.v-toolbar {
    background-color: #1e88e5 !important;
    color: #fff !important;
  }
*/
  .iconsize {
    width: 40px;
    height: 40px;
    border: 1px double #b6b6b6;
  }
  .mnl-5{
    margin-left: -2px;
  }

</style>



