<template>
  <div id="app" class="h-100p"  v-resize="onResize">
    <div class="container-fluid h-100p">
     <login v-if="gsw_ui === 'login'"></login> 
      <mainEvaluacion v-if="gsw_ui === 'mainEvaluacion'"></mainEvaluacion>
      <mensaje v-if="g_showMessage.show"></mensaje>

  

 <div class=" containersModal " v-if="g_Loading.show">
      <v-progress-circular v-if="g_Loading.show && !isMobile" :rotate="-100" :size="380" :width="55" :value="value" color="blue"  class="gray display-3 font-weight-bold center possition loadingz" >
        {{ value }}
      </v-progress-circular>

      <v-progress-circular v-if="g_Loading.show && isMobile" :rotate="-100" :size="250" :width="40" :value="value" color="blue"  class=" display-3 font-weight-bold center possition2 loadingz" >
        {{ value }}
      </v-progress-circular>
 </div>

    </div>
  </div>
</template>

<script>
  /*APPSS */
  import mainEvaluacion from './components/mainEvaluacion.vue';
  import login from './components/modules/login/login.vue';
  import mensaje from './components/elemento/mensaje.vue';
  export default {
    name: 'App',
    components: {
      mainEvaluacion,
      login,
      mensaje
    },
    data() {
      return {
         interval: {},
        value: 0,
        isMobile: false,
      }
    },
    computed: {
      gsw_ui() {
        return this.$store.getters.gsw_ui;
      },
      g_showMessage() {
        return this.$store.getters.g_showMessage;
      },
      g_Loading(){

        return this.$store.getters.g_Loading;
        
      }
    },
     beforeDestroy () {
     clearInterval(this.interval);
 
    },
    methods:{
       onResize() {
        if (window.innerWidth < 910)
          this.isMobile = true;
        else
          this.isMobile = false;
      }, 
    },
    mounted () {
      this.interval = setInterval(() => {
        if (!this.g_Loading.show) {
          return (this.value = 0)
        }
        this.value += 10
      }, 1500);
    }
  }
</script>

<style scoped>
  @import './assets/css/global.css';

   .v-progress-circular{
    margin: 1rem

   }

   .mt-200{
     margin-top: -50px;
   }


    .possition {
    position: fixed;
    top: 250px;
    bottom: 0;
    left: 500px;
    width: 22%;
    
  }
   .possition2 {   

    position: fixed;
    top: 300px;
    bottom: 0;
    left: 100px;   
    
  }

  .loadingz {
    z-index: 10;
  }

  .v-progress-circular__underlay {
    stroke: rgb(73, 86, 97);
    z-index: 1;
}
</style>


